<template>
    <Header :homeDisplay="this.homeDisplay"/>
    <h3> [ 휴일지정 ] </h3>
    <div class="tablepick">
        <form @submit.prevent="holidayhandleSubmit">
            <div class="mycal">
                <div class="justify-content-center">
                    <VDatePicker :attributes='attributes' @dayclick="onDayClick" mode="multiple" 
                    :disabled-dates="disabledDates" :color="selectedColor" :min-date='new Date()' 
                    :disabled-attribute='disabledAttribute' expanded class="my-calendar" />
                </div>
            </div>

            <div class="mb-3 border bg-warning border-warning rounded p-2" style="--bs-bg-opacity: 0.2" v-if="!!this.message">
                {{ this.message }}
            </div>

            <div class="footer">
                <div class="d-flex justify-content-center">
                    <button type="submit" class="btn btn-primary" size="lg" >설 정</button>
                </div>
            </div>
        </form>
    </div>
</template>

<script>
import {ref} from 'vue';
import Header from "@/components/Header.vue";
import router from "@/router";

var holidays= ref([]);
const selectedColor = ref('white');

const disabledDates = ref([{
        repeat: {
            weekdays: [1, 7],
        },
}]);

const disabledAttribute = ref([{
        contentStyle: {
            color: 'red',
        },
        contentHoverStyle: {
                cursor: 'default',
                backgroundColor: 'transparent',
        },
}]);

const homeDisplay = ref(true);

export default {
    name: "HolidayView",
    components: { 
        Header,
    },    
    data() {
            return {
                holidays: holidays,
                twin_holidays:[],
                date: new Date(),
                disabledDates,
                selectedColor,
                disabledAttribute,
                // newHoliday: {
                //     id: "",
                //     date: "",
                //     uid: "",
                //     created_at:0,
                // },
                message:"",
                timer: 1,
                interval: "",
                homeDisplay: homeDisplay,
            }
    },
    computed: {        
        fetchHolidayList() {
            return this.$store.state.holiday.holidayList;            
        },        
        holidates() {
            return this.holidays.map(day => day.date);
        },
        attributes() {
            return [
                {
                    contentStyle: {
                        fontWeight: '700',
                        fontSize: '.9rem',
                    },
                    dates: new Date(),
                },                
                ...this.holidates.map(date => ({
                    highlight: 'red',
                    dates: date,
                })),
            ];
        }        
    },
    watch:{
        fetchHolidayList(val){
            if (val.length>0) {
                this.holidays = val;            
                let tdays=[];
                val.forEach ( (day) => {
                    tdays.push(day);
                })
                this.twin_holidays = tdays;
            }            
        }
    },
    mounted() {
        this.$store.dispatch("fetchHolidayList");
    },    
    methods: {            
        onDayClick(day) {
            const now = new Date();
            const offset = new Date().getTimezoneOffset() * 60000
            const date = new Date(day.date - offset);
            
            let day_s = date.toISOString().split('T')[0];
            let nowday_s = now.toISOString().split('T')[0];

            if(  day_s >= nowday_s) {
                const idx = this.holidays.findIndex(d => d.id === day.id);            
                if (idx >= 0) {
                    this.holidays.splice(idx, 1);
                } else {
                    let weekend = this.dateDisabled(date);                    
                    if (!weekend) {

                        this.holidays.push({
                            id: day.id,
                            date: date.toISOString(),
                            uid:this.$store.state.auth.user.uid,
                            created_at:Date.now(),
                        });                     

                    }                    
                }
            }
        },
        async holidayhandleSubmit() {                
            let tmpdays = [];
            this.holidays.forEach( (day) => {
                const mydate = new Date(day.date);
                let weekend = this.dateDisabled(mydate);
                if (!weekend) {                         
                    tmpdays.push(day);
                }                    
            });
            tmpdays.forEach( (day) =>{
                const idx = this.twin_holidays.findIndex(d => d.id === day.id);
                if (idx === -1) {
                    this.$store.dispatch("addNewHoliday", day);
                    this.$store.dispatch("HolidayDeleteTicket", day.id);
                }
            });
            this.holidays = tmpdays;
            
            this.twin_holidays.forEach( (day)=>{                    
                const idx = this.holidays.findIndex(d => d.id === day.id);
                if (idx === -1) {
                    // console.log('please delete from firebase');
                    this.$store.dispatch("deleteHoliday", day.id);
                }
            });                
            this.message="휴일 설정이 완료 되었습니다";            
            this.startTimer();
            router.push('/home');
        },
            
        dateDisabled(date) {                
            const weekday = date.getDay()
            return weekday === 0 || weekday === 6
        },

        startTimer() {
            this.interval = setInterval(() => {
                if (this.timer === 0) {
                    clearInterval(this.interval)
                    this.message="";                
                } else {
                    this.timer--
                }             
            }, 700)
        }
    },
    
};
</script>

<style scoped>
.my-calendar .vc-weekday-1, .my-calendar .vc-weekday-7 {
    color: #f50303;
}

.my-calendar.vc-container {
    --day-border: 1px solid #b8c2cc;
    --day-border-highlight: 1px solid #b8c2cc;
    --day-width: 90px;
    --day-height: 90px;
    --weekday-bg: #f8fafc;
    --weekday-border: 1px solid #eaeaea;

    border-radius: 5%;
    width: 98%;
}
.tablepick {
    padding: 4%;
}
.mycal{
    padding: 10px;
}
.footer{
    padding: 20px;
}

h3 {
    padding: 10px;
    text-align: center;
}

</style>